
body,
html {
  /* width: 100%;
  height: 100%; */
  min-height: 500px;
  margin: 0;
  padding: 0;
}
#app {
  font-family: "SourceHanSansCN-Regular";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  width: 70%;
  /* height: 100%; */
  min-height: 500px;
  max-width: 1200px;
  margin: 0 auto;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  position: relative;
}
.navInit {
  padding: 2rem 0;
}
.numGroup {
  font-weight: normal;
  font-stretch: normal;
  color: #11bf5a;
  position: relative;
}
.numGroup > span {
  color: #11bf5a;
}
.setText {
  font-size: 14px;
  color: #666666;
  margin-left: 10px;
}
.el-select {
  width: 40%;
  margin: 3rem 0;
  margin-left: 20px;
}
.pattern {
  margin: 0 1rem;
  padding: 2rem 0;
  font-size: 14px;
  color: #666666;
}
.el-radio__label {
  font-size: 16px;
}
.operaBtn {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: wrap;
      flex-flow: wrap;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
}
.operaBtn-row {
  margin-right: 10%;
  text-align: right;
}
.showNum {
  background-color: #11bf5a;
  border-radius: 8px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  color: #414141;
}
.showNums {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 80%;
  /* height: 30rem; */
  /* margin-left: 1rem; */
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.showNums > div {
  /* width: 120px;
  height: 120px;
  font-size: 88px; */
  background-color: #11bf5a;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  color: #414141;
}
.el-button--default {
  width: 100%;
}
.el-button.is-round {
  border-radius: 45px !important;
}
.el-button:hover,
.el-button:focus {
  border-radius: 45px !important;
}
.el-button:active {
  border-radius: 45px !important;
}

/* 弹出框 */
.data-content {
  width: 20%;
}
.content-item {
  width: 40px;
  height: 40px;
  background-color: #11bf5a;
  border-radius: 18px;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.data-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 2% 5% 5%;
  color: #ffffff;
}
.navDialog {
  padding: 1rem 0 1.5rem;
}

